}
.f1-nav ul li a{
    font-size: 1rem;
    text-decoration: none;
    color: whitesmoke;
    font-weight: lighter;
}

.f1-nav ul li a:hover{
    background-image: url(/f1latamdemo/static/media/flag3.083cb1d5.jpg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    transition: 0.3s;
}

.menu-hamburger {
    display: none;
}


p {
    color: white;
}


.imgLogo {
    position: relative;
    margin-top: -10px;
    width: 160px;
}

